.dashboard-container {
  display: flex;
  height: 100vh;
  background: #f3f4f6;
}

/* Sidebar */
.sidebar {
  width: 260px;
  background: linear-gradient(180deg, #1e293b 0%, #0f172a 100%);
  color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
}

.logo-image {
  width: 100%;
  max-width: 140px;
  height: auto;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}

.sidebar-header {
  padding: 30px 20px;
  background: white;
  border-bottom: 1px solid #e5e7eb;
}

.sidebar .logo {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

.sidebar-nav {
  flex: 1;
  padding: 20px 0;
}

.nav-item {
  display: flex;
  align-items: center;
  padding: 14px 24px;
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: all 0.3s;
  font-size: 15px;
  font-weight: 500;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.nav-item.active {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border-left: 4px solid #f59e0b;
}

.nav-icon {
  margin-right: 12px;
  font-size: 18px;
}

.sidebar-footer {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.logout-btn {
  width: 100%;
  padding: 12px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.logout-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Main Content */
.main-content {
  flex: 1;
  overflow-y: auto;
  padding: 40px;
}

.content-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
}

.content-header h1 {
  font-size: 32px;
  color: #1f2937;
  margin-bottom: 8px;
}

.content-header p {
  color: #6b7280;
  font-size: 16px;
}

.user-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, #1e40af, #1e3a8a);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0 4px 12px rgba(30, 64, 175, 0.3);
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.stat-card {
  background: white;
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: transform 0.3s;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.stat-icon {
  font-size: 36px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e0f2fe, #dbeafe);
  border-radius: 12px;
}

.stat-value {
  font-size: 28px;
  font-weight: bold;
  color: #1f2937;
}

.stat-label {
  color: #6b7280;
  font-size: 14px;
  margin-top: 4px;
}

/* Actions Section */
.actions-section {
  margin-bottom: 40px;
}

.actions-section h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #1f2937;
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}

.action-card {
  background: white;
  border-radius: 16px;
  padding: 28px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  transition: all 0.3s;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.action-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.action-icon {
  font-size: 40px;
  margin-bottom: 16px;
}

.action-card h3 {
  font-size: 18px;
  margin-bottom: 8px;
  color: #1f2937;
}

.action-card p {
  color: #6b7280;
  font-size: 14px;
  line-height: 1.5;
}

/* Recent Section */
.recent-section h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #1f2937;
}

.uploads-table {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.upload-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #f3f4f6;
  transition: background 0.2s;
}

.upload-row:last-child {
  border-bottom: none;
}

.upload-row:hover {
  background: #f9fafb;
}

.upload-info {
  display: flex;
  align-items: center;
  gap: 16px;
}

.upload-icon {
  font-size: 24px;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: 12px;
}

.upload-name {
  font-weight: 500;
  color: #1f2937;
  margin-bottom: 4px;
}

.upload-meta {
  font-size: 13px;
  color: #9ca3af;
}

.upload-date {
  color: #6b7280;
  font-size: 14px;
}

/* Upload Page Specific */
.upload-container {
  max-width: 900px;
}

.device-selector {
  background: white;
  border-radius: 16px;
  padding: 28px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.device-selector h3 {
  margin-bottom: 20px;
  color: #1f2937;
}

.device-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.device-option {
  cursor: pointer;
}

.device-option input {
  display: none;
}

.device-card {
  padding: 20px;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  text-align: center;
  transition: all 0.3s;
}

.device-option.selected .device-card {
  border-color: #10b981;
  background: #ecfdf5;
}

.device-card:hover {
  border-color: #10b981;
}

.device-icon {
  font-size: 32px;
  margin-bottom: 12px;
}

.device-name {
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 4px;
}

.device-desc {
  font-size: 13px;
  color: #6b7280;
}

.upload-dropzone {
  background: white;
  border: 3px dashed #d1d5db;
  border-radius: 16px;
  padding: 60px 40px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
  margin-bottom: 24px;
}

.upload-dropzone:hover {
  border-color: #10b981;
  background: #f9fafb;
}

.upload-icon {
  font-size: 48px;
  margin-bottom: 16px;
}

.dropzone-content h3 {
  color: #1f2937;
  margin-bottom: 8px;
}

.dropzone-content p {
  color: #6b7280;
  margin-bottom: 16px;
}

.file-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.file-types {
  font-size: 13px;
  color: #9ca3af;
}

.selected-files {
  background: white;
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.selected-files h3 {
  margin-bottom: 16px;
  color: #1f2937;
}

.files-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.file-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: #f9fafb;
  border-radius: 10px;
}

.file-icon {
  font-size: 24px;
}

.file-info {
  flex: 1;
}

.file-name {
  font-weight: 500;
  color: #1f2937;
  font-size: 14px;
  margin-bottom: 2px;
}

.file-size {
  font-size: 12px;
  color: #6b7280;
}

.remove-file {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  background: #fee2e2;
  color: #dc2626;
  cursor: pointer;
  transition: all 0.2s;
}

.remove-file:hover {
  background: #fecaca;
}

.upload-progress {
  background: white;
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.progress-bar {
  height: 12px;
  background: #e5e7eb;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 12px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #10b981, #059669);
  transition: width 0.3s;
  border-radius: 10px;
}

.progress-text {
  text-align: center;
  color: #6b7280;
  font-size: 14px;
}

.btn-upload {
  width: 100%;
  padding: 16px;
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.btn-upload:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(16, 185, 129, 0.4);
}